@use "../../utilities/variables";
@use "../../utilities/mixins";

.ucla-header--school {
  border-top: 11px solid variables.$ucla-blue;
  border-bottom: 1px solid variables.$grey-20;
  min-height: 0;

  &__logo-link {
    text-decoration: none;
    line-height: 1;
    // margin-left: 0.375rem;
    font-size: 1.188rem;
    font-weight: bold;
    overflow-wrap: break-word;
    color: variables.$grey-80;
    display: flex;
    align-items: center;

    @include mixins.for-size(phone-only) {
    

      inline-size: 208px;
    }

    @include mixins.for-size(up-to-tablet) {
      margin-left: 1.5rem;
    }

    @include mixins.for-size(tablet-up) {
      font-size: 2rem;
    }
    & > svg {
      width: 6rem;
      height: 2.75rem;
      margin-right: 0.375rem;
      flex-shrink: 0;

      @include mixins.for-size(tablet-up) {
        width: 7.688rem;
        height: 3.563rem;
        margin-right: 0.625rem;
      }
    }

    &:hover {
      color: variables.$hover-state;
    }

    &:focus {
      outline: 2px solid variables.$focus-state;
      outline-offset: 2px;
      color: variables.$focus-state;
    }
  }

  .ucla-header__container {
    padding: 1rem 0;
    justify-content: flex-start;

    @include mixins.for-size(tablet-up) {
      padding: 0.938rem 0;
    }
    @include mixins.for-size(desktop-up) {
      padding: 0.938rem 1.5rem 0;
    }
  }

  .hamburger {
    margin-left: auto;
  }

  @include mixins.for-size(desktop-up) {
    min-height: 6.188rem;
  }
}
